/* Main Section */
.main {
  height: 80vh; /* Adjust height as needed */
  width: 100%; /* Full-width container */
  margin: 0; /* Center the container */
  padding: 10px; /* Add padding around the content */
  background-image: url('/public/images/web_top_2022_11.gif');
  background-size: contain; /* Fit the full image inside */
  background-repeat: no-repeat;
  background-position: center;
  background-color: #0872a8; /* Background color */
  border-radius: 0px; /* Optional rounded edges */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Optional shadow for style */
  display: flex;
  flex-direction: column; /* Stack the main image and smaller images vertically */
  align-items: center;
  justify-content: flex-start; /* Align content to the top */
  color: white;
  font-size: 36px;
}

/* Image Gallery Container */
.image-gallery {
  margin-top: 0px; /* Space between the main image and smaller images */
  display: flex; /* Align smaller images horizontally */
  justify-content: center; /* Center the images */
  gap: 15px; /* Space between images */
  background-color: #b8eeef;
}

/* General styling for image elements (if you use <img> tags) */
.image-gallery img {
  width: 300px; /* Set a fixed width for smaller images */
  height: auto; /* Maintain aspect ratio */
  border-radius: 8px; /* Optional rounded corners */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3); /* Optional shadow for style */
  cursor: pointer; /* Add pointer cursor for interactivity */
  transition: transform 0.3s ease; /* Smooth hover effect */
  margin: 30px;
}

.image-gallery img:hover {
  transform: scale(1.2); /* Slightly enlarge on hover */
}

/* Image Gallery Backgrounds (for divs with background images) */
.image-gallery .image1,
.image-gallery .image2,
.image-gallery .image3,
.image-gallery .image4 {
  width: 350px;  /* Set the width */
  height: 250px; /* Set the height */
  background-size: cover;
  background-position: center;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

/* Specific background images for each image div */
.image-gallery .image1 {
  background-image: url('/public/images/student_erika_haruto_osuke.jpg');
}

.image-gallery .image2 {
  background-image: url('/public/images/student_reisuke.jpg');
}

.image-gallery .image3 {
  background-image: url('/public/images/sutudent_aoi_DSC02296.jpg');
}

.image-gallery .image4 {
  background-image: url('/public/images/sutudent_hinata_DSC02012.jpg');
}
